import React, { Component } from 'react';
import bgi from '../images/bgimg.jpg'
import { connect } from 'react-redux';

class Landing extends Component {
    render() {
        return (
            <div style={{ backgroundImage: `url(${bgi})`, height: "100%", backgroundPosition: "center", backgroundRepeat: "no-repeat", backgroundSize: "cover" }}>
                <div style={{
                        flex: 1,
                        fontSize: 40,
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        fontFamily:"comicsans"
                    }}
                >
                    <b style={{margin: "200px 200px"}}>Email Services</b>
                </div>
                <div>
                <div className="row text-center">
                    <div className="col-lg-4 m-6 p-6" style={{marginLeft: 40}}>
                        <div className="card" style={{width: "18rem"}}>
                        <div className="card-body">
                            <h5 className="card-title"><b>Features</b></h5>
                            {/* <p className="card-text">Basic Plan</p> */}
                        </div>
                        <ul className="list-group list-group-flush ">
                            <li className="list-group-item">Send Bulk Emails In One Click</li>
                            <li className="list-group-item">Manage Sent Emails</li>
                            <li className="list-group-item">New Features Will Be Added Soon</li>
                        </ul>
                        <div className="card-body text-center">
                            <a href="/auth/google" className="btn btn-primary">Connect With Google</a>
                        </div>
                        </div>
                    </div>
                </div>
                </div>
            </div>
        );
            }
}


function mapStateToProps({ auth }) {
    return { auth };
}

export default connect(mapStateToProps)(Landing);

