
import React from 'react';
// import { Label } from 'reactstrap';

export default ({ input, label, meta: {touched, error} }) => {
    console.log("Survey Field",input);
    var ph;
    if(input.name === "title") {
        ph = 'Enter Title'
    } else if(input.name === "subject") {
        ph = 'Enter Subject'
    } else {
        ph = 'Enter List Of Emails Separated by , Eg: abc123@gmail.com, efg123@gmail.com'
    } 
    return (
        <div>
            <label>{label}</label>
            <input {...input} style = {{marginBottom: '5px'}} placeholder={ph}/>
            {/* <textarea name="Text1" cols="40" rows="5"></textarea> */}
            <div className="red-text" style = {{marginBottom: '20px'}}>
                {touched && error}
            </div>
        </div>
    );
}