import React, { Component } from 'react';
import { reduxForm, Field } from 'redux-form';
import SurveyField from './SurveyField';
import BodyField from './BodyField';
import { Link } from 'react-router-dom'; 
import validateEmails from '../../utils/validateEmails';
// import TextField from 'material-ui/TextField'
// import { RadioButton, RadioButtonGroup } from 'material-ui/RadioButton'
// import Checkbox from 'material-ui/Checkbox'
// import SelectField from 'material-ui/SelectField'
// import MenuItem from 'material-ui/MenuItem'

class SurveyForm extends Component {
    
    renderFields() {
        return (
            <div>
                <Field 
                    label = "Survey Title" 
                    type = "text" 
                    name = "title" 
                    component  = {SurveyField}
                />
                <Field 
                    label = "Survey Subject" 
                    type = "text" 
                    name = "subject" 
                    component  = {SurveyField}
                />
                <Field 
                    label = "Email Body" 
                    type = "text" 
                    name = "body" 
                    component  = {BodyField}
                />
                <Field 
                    label = "Recipient List" 
                    type = "text" 
                    name = "recipients" 
                    component  = {SurveyField}
                    // multiLine = {true}
                    // rows = {2}
                />
            </div>
        );
    }
    render() {
        return (
            <div>
                <h2>Create Survey</h2>
                <form onSubmit =  {this.props.handleSubmit(
                                    // (values) => { console.log(values) } 
                                    this.props.onSurveySubmit)} style={{margin: "50px"}}>
                    {this.renderFields()}
                    {/* <Field type = "text" name = "surveyTitle" component = "input" /> */}
                    <Link to = "/surveys" className = "red btn-flat left white-text">Cancel</Link>
                    <button type="submit" className = "teal btn-flat right white-text">
                        Next
                        <i className = "material-icons right">done</i>
                    </button>
                </form>
            </div>
        );
    }
}

function validate(values) {
    const errors  = {};
    
    errors.recipients = validateEmails(values.recipients || '');
    console.log(values);
    if(!values.title) {
        errors.title = 'You need to provide some title';
    }
    if(!values.subject) {
        errors.subject = 'You need to provide a subject';
    }
    if(!values.body) {
        errors.body = 'You need to provide a body';
    }
    if(!values.recipients) {
        errors.recipients = 'You need to provide a list of emails';
    }

    return errors;
}


// Redux Form helper
export default reduxForm({
    validate,
    form: 'surveyForm', 
    destroyOnUnmount: false,
})(SurveyForm);