import React , { Component } from 'react';
import { connect } from 'react-redux';
import { fetchSurveys }  from '../../actions';

class SurveyList extends Component {
    componentDidMount() {
        this.props.fetchSurveys(); 
    }

    renderSurveys() {
        if(this.props.surveys.length) {
            return this.props.surveys.reverse().map(survey => {
                return (

                    // <div className = "card blue-grey darken-1" key = {survey._id}>
                    //     <div className = "card-content">
                    //         <span className = "card-title">{survey.title}</span>
                    //         <p>
                    //             {survey.body}
                    //         </p>
                    //         <p className = "right">
                    //             Sent On: { new Date(survey.dateSent).toLocaleDateString() }
                    //         </p>
                    //     </div>
                    // </div>
                    <div class="card" style={{position: "relative", zIndex: "10px"}}>
                        <h5 class="card-header">Title:{survey.title}</h5>
                        <div class="card-body">
                            <h5 class="card-title">Subject: {survey.subject}</h5>
                            <p class="card-text"><b>Body: </b><pre>{survey.body}</pre></p>
                            <a href="/surveys/recipients" class="btn btn-primary">See Recipients</a>
                            <b className = "right">
                                Sent On: { new Date(survey.dateSent).toLocaleDateString() }
                            </b>
                        </div>
                    </div>
                );
            });
        }
    
        return (<div>
            You Have Not Sent Any Surveys<br/>
            Use Your Credits To Send Surveys<br/>
            <div className="form-inline">Send morethan 10,000  <i className="material-icons" style={{color: "grey"}}>email</i> Emails in Single Click</div>
        </div>);
    }

    render() {
        return (
            <div className="jumbotron">
                {this.renderSurveys()}
            </div>
        );
    }
}

function mapStateToProps(state) {
    return { surveys: state.surveys };
}

export default connect(mapStateToProps, { fetchSurveys })(SurveyList);